.leaflet-control-container {
  display: block;
}
@media only screen and (min-width: 768px) {
  .leaflet-control-container {
    height: 100%;
  }
}

.leaflet-control-container ~ .leaflet-control-container {
  display: block;
}

.leaflet-top, .leaflet-bottom {
  transition: transform 0.2s ease-in-out;
}

.leaflet-top:not(.leaflet-right) {
  align-items: center;
  display: flex;
  padding: 0.5rem;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) {
    background-color: var(--Neutral-300, #E5E5E6);
    gap: 1rem;
    justify-content: flex-end;
    padding: 1rem;
  }
}
.leaflet-top:not(.leaflet-right) > .leaflet-control {
  margin-block-start: 10px !important;
}
@media only screen and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) > .leaflet-control {
    margin-block-start: 0;
  }
}
.leaflet-top:not(.leaflet-right) > :last-child {
  margin-inline-end: 10px;
}
@media only screen and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) > :last-child {
    margin-inline-end: 0;
  }
}
@media only screen and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) > .leaflet-control {
    margin-top: 20px !important;
  }
}
@media only screen and (min-width: 768px) and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) > .leaflet-control {
    margin-block-start: 0 !important;
  }
}
@media only screen and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) > :last-child {
    margin-right: 20px;
  }
}
@media only screen and (min-width: 768px) and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) > :last-child {
    margin-right: 0;
  }
}
.leaflet-top:not(.leaflet-right) .leaflet-control-filters,
.leaflet-top:not(.leaflet-right) .leaflet-control-list-view {
  align-items: center;
  border: 1px solid var(--owc-openkaarten-streetmap--primary-color) !important;
  background-color: #fff;
  border-radius: 3px;
  display: flex;
  justify-content: space-between;
  gap: 8px;
  min-width: 44px;
  min-height: 50px;
  padding: 10px 24px;
  margin-left: 0 !important;
}
.leaflet-top:not(.leaflet-right) .leaflet-control-filters__control-text,
.leaflet-top:not(.leaflet-right) .leaflet-control-list-view__control-text {
  color: var(--owc-openkaarten-streetmap--primary-color);
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 130%; /* 26px */
}
.leaflet-top:not(.leaflet-right) .leaflet-control-filters:hover,
.leaflet-top:not(.leaflet-right) .leaflet-control-list-view:hover {
  cursor: pointer;
  background-color: rgb(244, 244, 244);
}
.leaflet-top:not(.leaflet-right) .leaflet-control-filters:focus-visible,
.leaflet-top:not(.leaflet-right) .leaflet-control-list-view:focus-visible {
  border-width: 3px;
}
.leaflet-top:not(.leaflet-right) .leaflet-control-filters a,
.leaflet-top:not(.leaflet-right) .leaflet-control-list-view a {
  width: 100%;
  height: 100%;
}
.leaflet-top:not(.leaflet-right) .leaflet-control-list-view {
  margin-inline-end: 1rem;
}
@media only screen and (min-width: 768px) {
  .leaflet-top:not(.leaflet-right) .leaflet-control-list-view {
    margin-inline-end: 0;
  }
}

.leaflet-bottom {
  bottom: auto;
  top: 1rem;
}
@media only screen and (min-width: 768px) {
  .leaflet-bottom {
    bottom: 0;
    top: auto;
  }
}
.leaflet-bottom .leaflet-control-zoom {
  align-items: center;
  border: 1px solid var(--owc-openkaarten-streetmap--primary-color) !important;
  border-radius: 3px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  margin-right: 8px;
}
.leaflet-bottom .leaflet-control-zoom a {
  align-items: center;
  border: none;
  border-radius: 3px;
  color: var(--owc-openkaarten-streetmap--primary-color);
  display: flex;
  height: 48px !important;
  justify-content: center;
  width: 44px !important;
}
.leaflet-bottom .leaflet-control-zoom a:first-child {
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 0;
  border-left: 1px solid var(--owc-openkaarten-streetmap--primary-color) !important;
  border-top-left-radius: 3px;
  border-top-right-radius: 0;
}